%text-gradient-h2 {
  h2 {
    font-size: 28px;
    @include text-gradient($c-linear);
    position: relative;

    &::before {
      content: " ";
      position: absolute;
      display: block;
      width: 50px;
      height: 7px;
      border-radius: 100px;
      background-color: $c-nav-active;
      bottom: -20px;
    }
  }
}

%text-gradient-h2-center {
  @extend %text-gradient-h2;
  h2 {
    text-align: center;
    display: block;

    &::before {
      right: 0;
      left: 0;
      margin: auto;
    }
  }
}

%text-gradient-p {
  p {
    color: $c-nav-link;
    line-height: 40px;
    margin: {
      top: 60px;
      bottom: 30px;
    }
  }
}

.col {
    font-size: 16px;

    &--light {
      font-size: 18px;
      margin-bottom: 30px;
    }
  
    /* Home component */
    // Col at First row
    &--home {
      @include block(35%, auto);
      
      margin: {
        top: 142px;
        left: 12%;
      }
  
      font-size: 40px;
      color: white;
  
      & span {
        font-weight: 700;
      }

    // Col at Second row
    
      &-2 {
        @include block(36.1%, auto);
        margin: {
          left: 3%;
        };

        @extend %text-gradient-h2;
        @extend %text-gradient-p;
        h2 {
          margin: {
            top: -32px;
            bottom: 0px;
          }
        }
      }

    // Col at fourth row
      &-4 {
        @include block(36%, auto);
        @extend %text-gradient-h2;
        @extend %text-gradient-p;

        margin: {
          top: -35px;
          left: 14%;
        }
      }
    // Col at fifth row
      &-5 {
        @include block(65.4%, 504px);
        background-color: white;
        opacity: 0.98;
        box-shadow: 0px 50px 100px rgba(0, 0, 0, 0.1);
        border-radius: 30px;
        margin: {
          left: auto;
          right: auto;
        }

        @extend %text-gradient-h2;
        @extend %text-gradient-h2-center;
        h2 {
          top: 80px;
        }

        div {
          padding: {
            top: 120px;
            left: 20%;
            right: 20%;
          }
          box-sizing: border-box;
          @include flex(0, space-between);
          p {
            @include block(262px, 226px);
            position: relative;
            border-radius: 15px;
            border: 2px solid transparent;
            background: white;
            background-clip: padding-box;
            position: relative;

            &::after {
              position: absolute;
              background: $c-linear;
              content: "";
              z-index: -1;
              top: -2px;
              left: -2px;
              right: -2px;
              bottom: -2px;
              border-radius: 15px;
            }

            img {
              @include absolute(-50px, 0px, 0px, 0px, auto);
            }

            span {
              @include block(100px, auto);
              @include absolute(0, 0px, 50px, 0px, auto);
              text-align: center;
              font-weight: 700;
            }
          }
        }
      }

    // Col at seventh-row 
      &-7 {
        @include block(100%, auto);
        @extend %text-gradient-h2-center;
        h2 {
          @include text-gradient(white);
          top: 540px;
        }
        @extend %text-gradient-p;
        p {
          @include block(36%, auto);
          color: white;
          margin: {
            top: 600px;
            left: auto;
            right: auto;
          }
          text-align: center;
        }

        img {
          margin: {
            top: 230px;
            left: 50%;
          };
        }
      }

    // Col at eighth row
      &-8 {
        @include block(100%, auto);

        @extend %text-gradient-h2-center;
        @extend %text-gradient-p;

        h2 {
          top: -80px;
        }

        p {
          text-align: center;
          margin-top: -25px;
        }
      }
    
    // Col at eighth row
      &-9 {
        @include block(100%, auto);
        @include flex(0, center);
        margin: {
          top: 110px;
        }

        & img {
          margin: {
            left: 15px;
            right: 15px;
          }
        }
      }

    // Col at tenth row
      &-10 {
        @include block(100%, auto);

        @extend %text-gradient-h2-center;
        @extend %text-gradient-p;

        p {
          text-align: center;
          margin-bottom: 70px;
        }
      }

    // Col at ninth row
      &-11 {
        @include block(40%, auto);
        @include flex();
        margin: auto;

        input {
          width: 100%;
          padding: 18px 20px;
          border-radius: 50px;
          outline: none;
          margin-right: 10px;
          border: double 3px transparent;
          background-image: $c-linear;
          background-origin: border-box;
          background-clip: padding-box, border-box;
        }
      }
    }
  }