.col-image {
  & img {
    max-width: 100%;
  }

  /* Home */   
  // Col at First row
  &--home {
    margin: {
      top: 60px;
      right: 5%;
    }

  // Col at Second row
    &-2 {
      @include block(42%, auto);
      margin: {
        top: -20px;
        left: 10%;
      }
    }

    &-4 {
      @include block(35%, auto);
      margin: {
        top: -45px;
        left: 4%;
      }
    }
  }
}